@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;700&display=swap");
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
}

.module {
  display: flex;
  flex-direction: column;
  max-width: 600px;
  margin: auto;
  text-align: center;
  margin-top: 4rem;
}
.module__image {
  height: auto;
  min-height: 200px;
  width: 100%;
}
.module__image img {
  width: 70%;
  height: auto;
}